<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process237"></a>

<p><br></p>
<p><span class="rvts17">神学院（</span><span class="rvts50">The Academy</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">主线任务</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:345,Y:821</span><span class="rvts21">，地点：神学院大门（</span><span class="rvts20">The Gate of Academy</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到进入神学院的方法，进入神学院，并想办法打开王者竞技场的大门</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><span class="rvts21">进入神学院的方法有两种，一种是从月之门，通过解密进入。另一种是找到一处隐藏的进入神学院秘密通道。</span>
<br><span class="rvts21">具体方法如下：</span>
<br><span class="rvts21">第一种方法：</span><span class="rvts20"> </span><span class="rvts21">从正门进入</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1320.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">从正门进入神学院的门，需要了解七神的日月派系，可以通过帮助魔导师亚历山大或帮助黑环首领（二选一），也可以自己探索所有的七处祭坛，获得其中的一些祭坛信息，具体所有的祭坛信息如下：</span>
<br><span class="rvts21">人类（瑞雷克，</span><span class="rvts20">Phalic</span><span class="rvts21">）祭坛：太阳</span>
<br><span class="rvts21">矮人（杜娜，</span><span class="rvts20">Duna</span><span class="rvts21">）祭坛：月亮</span>
<br><span class="rvts21">小鬼（赞提撒，</span><span class="rvts20">Xantessa</span><span class="rvts21">）祭坛：月亮</span>
<br><span class="rvts21">精灵（泰</span><span class="rvts20"></span><span class="rvts21">森迪刘斯，</span><span class="rvts20">Tir-cend</span><span class="rvts21">）祭坛：月亮</span>
<br><span class="rvts21">巫师（阿玛蒂亚，</span><span class="rvts20">Armadia</span><span class="rvts21">）祭坛：月亮</span>
<br><span class="rvts21">蜥蜴（祖尔</span><span class="rvts20"></span><span class="rvts21">斯提萨，</span><span class="rvts20">Zorl Stissa</span><span class="rvts21">）祭坛：太阳</span>
<br><span class="rvts21">兽人（维罗格，</span><span class="rvts20">Vrogir</span><span class="rvts21">）祭坛：太阳</span>
<br><span class="rvts21">把所有的祭坛日月派属性调整好后，来到大门处，把</span><span class="rvts20"></span><span class="rvts21">充能电容</span><span class="rvts20"></span><span class="rvts21">丢到电池板上，拉动闸刀，便可以进入神学院。</span><span class="rvts20"></span><span class="rvts21">充能电容</span><span class="rvts20"></span><span class="rvts21">可以在无名岛北部的一处沿海山洞中，通过打败那些石头守卫获得，石头守卫位于</span><span class="rvts20">X:251,Y:965</span><span class="rvts21">的山洞中。</span>
<br><span class="rvts21">如果没有</span><span class="rvts20"></span><span class="rvts21">充能电容</span><span class="rvts20"></span><span class="rvts21">，也可以使用气元素技能，使电池板充能（但只有几秒），经测试，气元素中较弱的</span><span class="rvts20"></span><span class="rvts21">电能释放（</span><span class="rvts20">Electric Discharge</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">等技能并不能使电池板充电，使用更高级别的</span><span class="rvts20"></span><span class="rvts21">连环闪电（</span><span class="rvts20">Chain Lightning</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">或</span><span class="rvts20"></span><span class="rvts21">闭路电流（</span><span class="rvts20">Closed Circuit</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">可以成功激活电池板。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">第二种方法：从神学院秘密通道进入</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1321.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该洞穴位于无名岛东部荒芜地带，经历此处需要穿越很多悬崖陡壁和亡灵火焰。角色需要使用跳跃，飞翔等技能才能达到。具体位置在</span><span class="rvts20">X:378,Y:695</span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1322.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入神学院后的主要任务是打开王者竞技场的大门。如图，桌子上有一个</span><span class="rvts20"></span><span class="rvts21">充能导体</span><span class="rvts20"></span><span class="rvts21">，石头看守身上也有这种物品。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1323.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">把能量导体移动到电池板上，便可以激活反光通道。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1324.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">调整镜子的方向，让能量源源不断的折射下去。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1325.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最终成功激活竞技场大门最高处的那块电池板。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1326.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最后，注意地面还有两处电池板，使它们充能后（使用气元素技能，或使用额外的</span><span class="rvts20"></span><span class="rvts21">充能导体</span><span class="rvts20"></span><span class="rvts21">），拉下它旁边的闸刀，大门便开启。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1327.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">一旦王者竞技场的大门打开，便会在里面遇到所有的源力者竞争对手（他们是我们未选取的伙伴，或在学院门口未说服并解散的伙伴），注意，如果我们在进入学院大门前，亚历山大依然活着，他会从精灵营地消失，并来到这里，与我们一起竞争王者。然而，令人出乎意料的是，如果使用队伍中具有较高劝说值的角色尝试说服他辅佐你称王，他竟然很有可能同意（帮助你，杀掉其它的竞争者）。这将解锁</span><span class="rvts20"></span><span class="rvts21">受欢迎的孩子（</span><span class="rvts20">Favoured Son</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">成就。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1328.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">另外，神学院的议会大厅，很多原先的神醒者由于受到卢西安的谋害，死在了这里。</span>
<br><span class="rvts21">和他们的灵魂谈话，他们会教你增强一些特定的基础属性，但是这以牺牲你其它的属性为代价，具体如下：</span>
<br><span class="rvts20">1. </span><span class="rvts21">权威大师（</span><span class="rvts20">Spirit of Authority Master</span><span class="rvts21">）：</span><span class="rvts20">+5</span><span class="rvts21">记忆，</span><span class="rvts20">-5</span><span class="rvts21">力量；</span>
<br><span class="rvts20">2. </span><span class="rvts21">智慧大师（</span><span class="rvts20">Spirit of Intelligence Master</span><span class="rvts21">）：</span><span class="rvts20">+5</span><span class="rvts21">智慧，</span><span class="rvts20">-5</span><span class="rvts21">灵巧；</span>
<br><span class="rvts20">3. </span><span class="rvts21">智谋大师（</span><span class="rvts20">Spirit of Wits Master</span><span class="rvts21">）：</span><span class="rvts20">+5</span><span class="rvts21">智谋，</span><span class="rvts20">-5</span><span class="rvts21">体质；</span>
<br><span class="rvts20">4. </span><span class="rvts21">体质大师（</span><span class="rvts20">Spirit of the Constitution Master Master</span><span class="rvts21">）：</span><span class="rvts20">+5</span><span class="rvts21">体质，</span><span class="rvts20">-5</span><span class="rvts21">智慧；</span>
<br><span class="rvts20">5. </span><span class="rvts21">力量大师（</span><span class="rvts20">Spirit of Strength Master</span><span class="rvts21">）：</span><span class="rvts20">+5</span><span class="rvts21">力量，</span><span class="rvts20">-5</span><span class="rvts21">智力（</span><span class="rvts20">Wits</span><span class="rvts21">）。</span>
<br><span class="rvts21">所有的大师只会为队伍中的一个角色教一次技能，因此，注意角色的搭配和对话的选择。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1329.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在靠近王者竞技场附近，有一处机器人，使用</span><span class="rvts20"></span><span class="rvts21">充能导体</span><span class="rvts20"></span><span class="rvts21">可以激活该机器人。该机器人会告诉你学院的一些基础介绍，并出售很多技能书和装备物品。有趣的是，你可以通过</span><span class="rvts20"></span><span class="rvts21">偷盗</span><span class="rvts20"></span><span class="rvts21">，盗取他身上的钱币和商品，并且它并不会发觉此事。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1330.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在图书馆这里，会找到两处学习碑铭。在这里可以学到</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">。如果队伍在进入学院大门前，一些源力队友未被劝服而离队，那么可以回到复仇女神号，找到船甲板处的一个蜥蜴</span><span class="rvts20">NPC</span><span class="rvts21">，花钱请雇佣兵，来填充队伍的人员不足。</span>
<br><span class="rvts21">雇佣兵被招来以后，并不会</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">源力汲取</span><span class="rvts20"></span><span class="rvts21">技能，让他们读这两个碑铭，便可以让他们学到这两项技能。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



